import * as React from "react";
import { Breadcrumbs, List, ListItem, ListItemText, Typography } from "@material-ui/core";
import Navbar from "shared/components/ui/Navbar";
import { Link } from "gatsby";
import { navigate } from "gatsby";
import { useFetcher } from "shared/components/ajax";
import Defer from "shared/components/ui/Defer";
import { keyedCategories } from ".";

const Page = () => {
    var list = useFetcher('/desa/mail/services');
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Link color="inherit" to="/desa/surat">
                            Layanan Surat
                        </Link>
                        <Typography color="textPrimary">Buat</Typography>
                    </Breadcrumbs>
                </ListItem>
                <Defer data={list}>
                    {() => (
                        list.data.map(item => (<ListItem key={item.id} button onClick={() => navigate('/desa/surat/post?code='+item.code)}>
                            {keyedCategories[item.category] && React.createElement(keyedCategories[item.category].icon, { style: { fontSize: '2em', marginRight: 15 } })}
                            <ListItemText primary={item.title} secondary={item.description} />
                        </ListItem>))
                    )}
                </Defer>
            </List>
        </div>
    );
}

export default Page
